___CSS_LOADER_EXPORT___.push([module.id, "table caption{padding-left:10px;padding-right:10px;text-align:center}", "",{"version":3,"sources":["webpack://./resources/js/pages/accounting/SummaryPage.vue"],"names":[],"mappings":"AA6dA,cACA,iBAAA,CACA,kBAAA,CACA,iBACA","sourcesContent":["<template>\r\n    <div>\r\n        <alert-with-retry :value=\"errorText\" @retry=\"fetchData\" />\r\n        <div class=\"row mb-2\">\r\n            <div class=\"col-sm\">\r\n                <div class=\"form-row\">\r\n                    <div class=\"col-auto mb-2\">\r\n                        <b-input-group :prepend=\"$t('Wallet')\">\r\n                            <b-select\r\n                                v-model=\"wallet\"\r\n                                :options=\"walletOptions\"\r\n                                :disabled=\"isBusy\"\r\n                            />\r\n                        </b-input-group>\r\n                    </div>\r\n                    <div v-if=\"allProjects.length > 0\" class=\"col-auto mb-2\">\r\n                        <b-select\r\n                            v-model=\"project\"\r\n                            :options=\"projectOptions\"\r\n                            :disabled=\"isBusy\"\r\n                        />\r\n                    </div>\r\n                    <div\r\n                        v-if=\"useLocations && allLocations.length\"\r\n                        class=\"col-auto mb-2\"\r\n                    >\r\n                        <b-select\r\n                            v-model=\"location\"\r\n                            :options=\"locationOptions\"\r\n                            :disabled=\"isBusy\"\r\n                        />\r\n                    </div>\r\n                </div>\r\n            </div>\r\n            <div class=\"col-sm-8 col-md-9 col-lg-10 col-xl-auto\">\r\n                <div class=\"form-row\">\r\n                    <div class=\"col-auto mb-2\">\r\n                        <b-button\r\n                            variant=\"secondary\"\r\n                            @click=\"setToCurrentMonth()\"\r\n                            ><font-awesome-icon icon=\"calendar-alt\"\r\n                        /></b-button>\r\n                    </div>\r\n                    <div class=\"col-auto mb-2\">\r\n                        <b-select\r\n                            v-model=\"month\"\r\n                            :options=\"monthOptions\"\r\n                            :disabled=\"isBusy\"\r\n                        />\r\n                    </div>\r\n                    <div class=\"col-auto mb-2\">\r\n                        <b-select\r\n                            v-model=\"year\"\r\n                            :options=\"yearOptions\"\r\n                            :disabled=\"isBusy\"\r\n                        />\r\n                    </div>\r\n                </div>\r\n            </div>\r\n        </div>\r\n\r\n        <!-- Wallets -->\r\n        <b-table\r\n            v-if=\"!isBusy && isLoaded\"\r\n            hover\r\n            responsive\r\n            :items=\"wallets\"\r\n            :fields=\"walletFields\"\r\n            class=\"shadow-sm mb-4 bg-white\"\r\n            :caption=\"heading\"\r\n            caption-top\r\n        >\r\n            <template #cell(name)=\"data\">\r\n                <router-link\r\n                    v-if=\"can('view-transactions')\"\r\n                    :to=\"{\r\n                        name: 'accounting.transactions.index',\r\n                        params: {\r\n                            wallet: data.item.id\r\n                        },\r\n                        query: {\r\n                            'filter[date_start]': filterDateStart,\r\n                            'filter[date_end]': filterDateEnd\r\n                        }\r\n                    }\"\r\n                >\r\n                    {{ data.value }}\r\n                </router-link>\r\n                <template v-else>\r\n                    {{ data.value }}\r\n                </template>\r\n            </template>\r\n            <template #custom-foot>\r\n                <b-tr>\r\n                    <b-td>\r\n                        <strong>{{ $t(\"Sum across all wallets\") }}</strong>\r\n                    </b-td>\r\n                    <b-td class=\"text-right\">\r\n                        <strong>{{\r\n                            totals.income | decimalNumberFormat\r\n                        }}</strong>\r\n                    </b-td>\r\n                    <b-td class=\"text-right\">\r\n                        <strong>{{\r\n                            totals.spending | decimalNumberFormat\r\n                        }}</strong>\r\n                    </b-td>\r\n                    <b-td class=\"text-right\">\r\n                        <strong>{{\r\n                            (totals.income - totals.spending)\r\n                                | decimalNumberFormat\r\n                        }}</strong>\r\n                    </b-td>\r\n                    <b-td class=\"text-right\">\r\n                        <strong>{{ totals.fees | decimalNumberFormat }}</strong>\r\n                    </b-td>\r\n                    <b-td class=\"text-right\">\r\n                        <strong>{{\r\n                            totals.amount | decimalNumberFormat\r\n                        }}</strong>\r\n                    </b-td>\r\n                </b-tr>\r\n            </template>\r\n        </b-table>\r\n\r\n        <template v-if=\"!isBusy && isLoaded\">\r\n            <b-row>\r\n                <!-- Revenue by categories -->\r\n                <b-col md>\r\n                    <SummaryList\r\n                        :items=\"categories\"\r\n                        :title=\"$t('Categories')\"\r\n                        paramName=\"category_id\"\r\n                        :wallet=\"wallet\"\r\n                        :filterDateStart=\"filterDateStart\"\r\n                        :filterDateEnd=\"filterDateEnd\"\r\n                        flatten-children\r\n                        show-donations\r\n                    />\r\n                </b-col>\r\n                <!-- Revenue by project -->\r\n                <b-col md>\r\n                    <SummaryList\r\n                        :items=\"projects\"\r\n                        :title=\"$t('Projects')\"\r\n                        paramName=\"project_id\"\r\n                        :noNameLabel=\"$t('No project')\"\r\n                        :wallet=\"wallet\"\r\n                        :filterDateStart=\"filterDateStart\"\r\n                        :filterDateEnd=\"filterDateEnd\"\r\n                        flatten-children\r\n                    />\r\n                </b-col>\r\n            </b-row>\r\n            <b-row>\r\n                <!-- Revenue by secondary category -->\r\n                <b-col v-if=\"secondCategories\" md>\r\n                    <SummaryList\r\n                        :items=\"secondCategories\"\r\n                        :title=\"$t('Secondary Categories')\"\r\n                        paramName=\"secondary_category\"\r\n                        :noNameLabel=\"$t('No Secondary Category')\"\r\n                        :wallet=\"wallet\"\r\n                        :filterDateStart=\"filterDateStart\"\r\n                        :filterDateEnd=\"filterDateEnd\"\r\n                    />\r\n                </b-col>\r\n                <!-- Revenue by location -->\r\n                <b-col v-if=\"locations\" md>\r\n                    <SummaryList\r\n                        :items=\"locations\"\r\n                        :title=\"$t('Locations')\"\r\n                        paramName=\"location\"\r\n                        :noNameLabel=\"$t('No location')\"\r\n                        :wallet=\"wallet\"\r\n                        :filterDateStart=\"filterDateStart\"\r\n                        :filterDateEnd=\"filterDateEnd\"\r\n                    />\r\n                </b-col>\r\n            </b-row>\r\n        </template>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\nimport moment from \"moment\";\r\nimport summaryApi from \"@/api/accounting/summary\";\r\nimport walletsApi from \"@/api/accounting/wallets\";\r\nimport transactionsApi from \"@/api/accounting/transactions\";\r\nimport projectsApi from \"@/api/accounting/projects\";\r\nimport AlertWithRetry from \"@/components/alerts/AlertWithRetry\";\r\nimport SummaryList from \"@/components/accounting/SummaryList\";\r\nimport { can } from \"@/plugins/laravel\";\r\nexport default {\r\n    components: {\r\n        AlertWithRetry,\r\n        SummaryList\r\n    },\r\n    data() {\r\n        return {\r\n            year: this.$route.query.year ?? null,\r\n            month: this.$route.query.month ? this.$route.query.month - 1 : null,\r\n            years: [moment().year()],\r\n\r\n            wallets: [],\r\n            totals: {},\r\n            categories: [],\r\n            projects: [],\r\n            secondCategories: [],\r\n            locations: [],\r\n\r\n            allWallets: [],\r\n            wallet: this.$route.query.wallet ?? null,\r\n            allProjects: [],\r\n            project: this.$route.query.project ?? null,\r\n\r\n            useLocations: false,\r\n            allLocations: [],\r\n            location: this.$route.query.location ?? null,\r\n\r\n            isLoaded: false,\r\n            isBusy: false,\r\n            errorText: null,\r\n\r\n            walletFields: [\r\n                {\r\n                    key: \"name\",\r\n                    label: this.$t(\"Wallet\")\r\n                },\r\n                {\r\n                    key: \"income\",\r\n                    label: this.$t(\"Income\"),\r\n                    class: \"text-right\",\r\n                    formatter: this.decimalNumberFormat\r\n                },\r\n                {\r\n                    key: \"spending\",\r\n                    label: this.$t(\"Spending\"),\r\n                    class: \"text-right\",\r\n                    formatter: this.decimalNumberFormat\r\n                },\r\n                {\r\n                    key: \"difference\",\r\n                    label: this.$t(\"Difference\"),\r\n                    class: \"text-right\",\r\n                    formatter: (value, key, item) =>\r\n                        this.decimalNumberFormat(item.income - item.spending),\r\n                    tdClass: (value, key, item) =>\r\n                        this.colorClass(item.income - item.spending > 0)\r\n                },\r\n                {\r\n                    key: \"fees\",\r\n                    label: this.$t(\"Fees\"),\r\n                    class: \"text-right\",\r\n                    formatter: this.decimalNumberFormat\r\n                },\r\n                {\r\n                    key: \"amount\",\r\n                    label: this.$t(\"Balance\"),\r\n                    class: \"text-right\",\r\n                    formatter: this.decimalNumberFormat,\r\n                    tdClass: (value, key, item) => this.colorClass(value > 0)\r\n                }\r\n            ]\r\n        };\r\n    },\r\n    computed: {\r\n        heading() {\r\n            let str = \"\";\r\n            if (this.year != null && this.month != null) {\r\n                const date = moment(\r\n                    `${this.year}-${this.month + 1}`,\r\n                    \"YYYY-M\"\r\n                ).startOf(\"month\");\r\n                str += date.format(\"MMMM YYYY\");\r\n            } else if (this.year != null) {\r\n                str += this.year;\r\n            } else {\r\n                str += this.$t(\"All time\");\r\n            }\r\n            return str;\r\n        },\r\n        monthOptions() {\r\n            let arr = [\r\n                {\r\n                    value: null,\r\n                    text: `- ${this.$t(\"Month\")} -`\r\n                }\r\n            ];\r\n            arr.push(\r\n                ...moment.months().map((month, idx) => ({\r\n                    value: idx,\r\n                    text: month\r\n                }))\r\n            );\r\n            return arr;\r\n        },\r\n        yearOptions() {\r\n            let arr = [\r\n                {\r\n                    value: null,\r\n                    text: `- ${this.$t(\"Year\")} -`\r\n                }\r\n            ];\r\n            arr.push(\r\n                ...this.years.map(year => ({\r\n                    value: year,\r\n                    text: year\r\n                }))\r\n            );\r\n            return arr;\r\n        },\r\n        walletOptions() {\r\n            let arr = [\r\n                {\r\n                    value: null,\r\n                    text: `- ${this.$t(\"All wallets\")} -`\r\n                }\r\n            ];\r\n            arr.push(\r\n                ...this.allWallets.map(e => ({\r\n                    value: e.id,\r\n                    text: e.name\r\n                }))\r\n            );\r\n            return arr;\r\n        },\r\n        projectOptions() {\r\n            let arr = [\r\n                {\r\n                    value: null,\r\n                    text: `- ${this.$t(\"All projects\")} -`\r\n                }\r\n            ];\r\n            for (let elem of this.allProjects) {\r\n                this.fillTree(arr, elem);\r\n            }\r\n            return arr;\r\n        },\r\n        locationOptions() {\r\n            let arr = [\r\n                {\r\n                    value: null,\r\n                    text: `- ${this.$t(\"All locations\")} -`\r\n                }\r\n            ];\r\n            arr.push(\r\n                ...this.allLocations.map(e => ({\r\n                    value: e,\r\n                    text: e\r\n                }))\r\n            );\r\n            return arr;\r\n        },\r\n        filterDateStart() {\r\n            if (this.year != null && this.month != null) {\r\n                return moment(`${this.year}-${this.month + 1}`, \"YYYY-M\")\r\n                    .startOf(\"month\")\r\n                    .format(moment.HTML5_FMT.DATE);\r\n            } else if (this.year != null) {\r\n                return moment(`${this.year}`, \"YYYY\")\r\n                    .startOf(\"year\")\r\n                    .format(moment.HTML5_FMT.DATE);\r\n            }\r\n            return null;\r\n        },\r\n        filterDateEnd() {\r\n            if (this.year != null && this.month != null) {\r\n                return moment(`${this.year}-${this.month + 1}`, \"YYYY-M\")\r\n                    .endOf(\"month\")\r\n                    .format(moment.HTML5_FMT.DATE);\r\n            } else if (this.year != null) {\r\n                return moment(`${this.year}`, \"YYYY\")\r\n                    .endOf(\"year\")\r\n                    .format(moment.HTML5_FMT.DATE);\r\n            }\r\n            return null;\r\n        }\r\n    },\r\n    watch: {\r\n        month(val) {\r\n            this.fetchData();\r\n        },\r\n        year(val) {\r\n            this.fetchData();\r\n        },\r\n        wallet(val) {\r\n            this.fetchData();\r\n        },\r\n        project(val) {\r\n            this.fetchData();\r\n        },\r\n        location(val) {\r\n            this.fetchData();\r\n        }\r\n    },\r\n    async created() {\r\n        this.allWallets = (await walletsApi.list()).data;\r\n        this.allProjects = await projectsApi.tree();\r\n        this.allLocations = await transactionsApi.locations();\r\n\r\n        await this.fetchData();\r\n        this.isLoaded = true;\r\n    },\r\n    methods: {\r\n        fillTree(tree, elem, level = 0) {\r\n            let text = \"\";\r\n            if (level > 0) {\r\n                text += \"&nbsp;\".repeat(level * 5);\r\n            }\r\n            text += elem.name;\r\n            tree.push({\r\n                html: text,\r\n                value: elem.id\r\n            });\r\n            for (let child of elem.children) {\r\n                this.fillTree(tree, child, level + 1);\r\n            }\r\n        },\r\n        setToCurrentMonth() {\r\n            const now = moment();\r\n            this.year = now.year();\r\n            this.month = now.month();\r\n        },\r\n        colorClass(value) {\r\n            return value > 0 ? \"text-success\" : \"text-danger\";\r\n        },\r\n        can,\r\n        async fetchData() {\r\n            this.$router.push(\r\n                {\r\n                    query: {\r\n                        ...this.$route.query,\r\n                        month: this.month !== null ? this.month + 1 : undefined,\r\n                        year: this.year || undefined,\r\n                        wallet: this.wallet || undefined,\r\n                        project: this.project || undefined,\r\n                        location: this.location || undefined\r\n                    }\r\n                },\r\n                () => {}\r\n            );\r\n\r\n            this.errorText = null;\r\n            this.isBusy = true;\r\n            try {\r\n                let data = await summaryApi.list({\r\n                    year: this.year,\r\n                    month: this.month !== null ? this.month + 1 : undefined,\r\n                    wallet: this.wallet,\r\n                    project: this.project,\r\n                    location: this.location\r\n                });\r\n\r\n                this.years = data.years;\r\n\r\n                this.wallets = data.wallets;\r\n                this.totals = data.totals;\r\n\r\n                this.projects = data.projects;\r\n                this.categories = data.categories;\r\n\r\n                this.secondCategories = data.second_categories;\r\n                this.locations = data.locations;\r\n\r\n                this.useLocations = data.use_locations;\r\n\r\n                this.isBusy = false;\r\n            } catch (err) {\r\n                this.errorText = err;\r\n            }\r\n        }\r\n    }\r\n};\r\n</script>\r\n\r\n<style>\r\ntable caption {\r\n    padding-left: 10px;\r\n    padding-right: 10px;\r\n    text-align: center;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
